
.dragWrap{
    display: flex;
    justify-content: center;
    align-items: center;

    height: 80px;
    width: 245px;
}
.drop, .drag {
    display: flex;
    justify-content: center;
    align-items: center;

    border: black dashed 1px;
    border-radius: 10px;
    height: 70%;
    width:  70%;
}
.drag{
    border: black solid 1px;
    box-shadow: 0 0 10px rgba(40, 55, 141, 0.2);
}